:root {
    --PrimaryColor: hsl(190, 100%, 50%);
    --HoverColor: #90ecff;
    --PaleColor: hsl(190, 72%, 70%);
    --WhiteColor: hsl(0, 0%, 100%);
    --BlackColor: hsl(0, 0%, 18%);
    --TextColor: hsl(240, 1%, 48%);
    --BgColor: hsl(220, 10%, 94%);
    --GreyText: rgb(190, 190, 190);
    --InputColor: hsl(330, 12%, 97%);

    --BiggestFontSize: 2.5rem;
    --h1FontSize: 1.5rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --NormalFontSize: .938rem;
    --SmallFontSize: .813rem;
    --SmallestFontSize: .75rem;
}


.topSection {
    position: fixed;
    background-color: white;
    width: 100%;
    z-index: 1;

    .topDiv {
        display: relative;
        padding: 1rem 3rem 0.5rem;

        .searchDiv {
            padding: 0 1rem;
            float: left;
            display: flex;

            .small-icon {
                font-size: var(--h1FontSize) !important;
                color: var(--BlackColor);
            }
        }

        .logo {
            margin: 0;
            position: absolute;
            left: 50%;
            top: 20%;
            transform: translate(-50%, -50%);
            color: var(--BlackColor);

            a {
                text-decoration: none;
                color: var(--BlackColor);
            }
        }

        .iconDiv {
            display: flex;
            float: right;
            gap: 1rem;
        }
    }


    nav ul {
        display: flex;
        justify-content: center;
        gap: 3rem;
        margin: 3rem 4rem 1rem;

        li {
            font-weight: 600;
            list-style-type: none;
            color: var(--BlackColor);

            a {
                &:hover {
                    color: red;
                }
            }
        }
    }

    .banner {
        background-color: black;
        text-align: center;
        color: white;
        padding: .6rem 0;
        font-size: var(--SmallFontSize);
    }
}





@media only screen and (max-width: 1024px) {
    .topSection {
        .topDiv {
            margin-bottom: 2rem;
            padding: 1rem 3rem;

            .searchDiv {
                border: none;
                width: 60px;

                input {
                    display: none;
                }

                .small-icon {
                    font-size: var(--h1FontSize) !important;
                    color: var(--BlackColor);
                    margin-bottom: .5rem;
                }
            }

            .logo {
                font-size: var(--SmallestFontSize);
                top: 30%;
            }

            .iconDiv {
                .linkText {

                    .person,
                    .heart {
                        display: none;
                    }
                }
            }
        }
    }


    nav ul {
        display: none !important;
    }
}


@media only screen and (max-width: 896px) {
    .topSection {
        .topDiv {
            margin-bottom: 1rem;

            .searchDiv {
                border: none;
                width: 60px;

                input {
                    display: none;
                }

                .small-icon {
                    font-size: var(--h1FontSize) !important;
                    color: var(--BlackColor);
                    margin-bottom: .5rem;
                }
            }

            .logo {
                font-size: var(--SmallestFontSize);
                top: 30%;
            }
        }
    }


    nav ul {
        display: none !important;
    }

}